import React, { useContext } from "react";
import Carousel from "react-bootstrap/Carousel";
import { LanguageContext } from "../../../common/languageContext";
import { useSelector } from "react-redux";

function MainCarousel() {
  const [, language] = useContext(LanguageContext);
  const slides = useSelector((state) => state.carousel.slides);
  const carouselItems = slides.map((slide) => (
    <Carousel.Item
      key={slide.img}
      // style={{
      //   height: "75vh",
      //   alignContent: "center",
      // }}
    >
      <img
        className="d-block w-100"
        src={slide.img}
        alt={slide[`caption_${language}`].paragraph}
      />
      {/*<div*/}
      {/*  style={{*/}
      {/*    backgroundBlendMode: "multiply",*/}
      {/*    backgroundColor: "rgba(200, 200, 200, .8)",*/}
      {/*    backgroundImage: `url(${slide.img})`,*/}
      {/*    height: "100%",*/}
      {/*    width: "100%",*/}
      {/*    backgroundSize: "100% auto",*/}
      {/*    backgroundRepeat: "no-repeat",*/}
      {/*    backgroundPosition: "center center",*/}
      {/*  }}*/}
      {/*></div>*/}
      <Carousel.Caption>
        <h3>{slide[`caption_${language}`].header}</h3>
        <p>{slide[`caption_${language}`].paragraph}</p>
      </Carousel.Caption>
    </Carousel.Item>
  ));
  return (
    <Carousel fade pause={false}>
      {carouselItems}
    </Carousel>
  );
}

export default MainCarousel;
